import { Link } from 'gatsby'
import PropTypes from 'prop-types'
import React from 'react'
import styled from 'styled-components'

import siteLogo from '../images/mattortiz.com - logo.png'

const HeaderWrapper = styled.div`
  background: #242a33;
  // margin: 0 auto;
  padding: 0.5rem;
  // max-width: 960px;
  img {
    margin-bottom: 0;
  }
`

const Header = ({ siteTitle }) => (
  <HeaderWrapper>
    <Link
      to="/"
      style={{
        color: 'white',
        textDecoration: 'none',
      }}
    >
      <img
        style={{
          width: '80px',
        }}
        src={siteLogo}
        alt="Matt Ortiz Logo"
      />
    </Link>
  </HeaderWrapper>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: '',
}

export default Header
